import Button from "../UI/Button";
import classes from "./ButtonList.module.css";

const ButtonList = (props) => {
  const homeHandler = () => {
    props.pageHandler("HOME");
  };
  const aboutHandler = () => {
    props.pageHandler("ABOUT");
  };
  const projectsHandler = () => {
    props.pageHandler("PROJECTS");
  };
  const contactHandler = () => {
    props.pageHandler("CONTACT");
  };

  return (
    <div className={classes["button-list"]}>
       <Button onClick={homeHandler}>HOME</Button>
      <Button onClick={aboutHandler}>ABOUT ME</Button>
      <Button onClick={projectsHandler}>PROJECTS</Button>
      <Button onClick={contactHandler}>CONTACT</Button>
    </div>
  );
};

export default ButtonList;
