import { Fragment } from "react";
import Header from "../../UI/Header";
import classes from "./About.module.css";
import Card from "../../UI/Card";
const About = () => {
  return (
    <div className={classes["about-content"]}>
      <Fragment>
        <Header title="About me" />
        <div className={classes.skills}>
          <Card>
            <h3>Computer Engineering Student</h3>
            <p>Ferdowsi University</p>
            <p>2017 - 2021</p>
            <p>
              {" "}
              <br />
            </p>
          </Card>
          <Card>
            <h3>Languages</h3>
            <p>Python</p>
            <p>C++</p>
            <p>JavaScript</p>
          </Card>

          <Card>
            <h3>Skills</h3>
            <p>Web development</p>
            <p>Teacher Assistant</p>
            <p>TeamWork</p>
          </Card>
          <Card>
            <h3>Technologies</h3>
            <p>React</p>
            <p>Git</p>
            <p>Postgres</p>
          </Card>
        </div>
      </Fragment>
    </div>
  );
};

export default About;
