import Header from "../../UI/Header";
import "./Contact.css";
import { Fragment, useState } from "react";
import useInput from "../../../hooks/use-input";
import Card from "../../UI/Card";
const Contact = () => {
  const [text, setText] = useState("");
  let showButton = false;
  const {
    value: name,
    error: nameError,
    reset: resetName,
    validValue: validName,
    valueInputHandler: nameHandler,
    valueBlurHandler: nameBlurHandler,
  } = useInput((value) => value.trim() !== "");

  const {
    value: email,
    error: emailError,
    reset: resetEmail,
    validValue: validEmail,
    valueInputHandler: emailHandler,
    valueBlurHandler: emailBlurHandler,
  } = useInput((x) => x.includes("@"));

  if (validName && validEmail) {
    showButton = true;
  }

  const onMessage = (event) => {
    setText(event.target.value);
  };

  const submitHandler = async (event) => {
    event.preventDefault();
    if (validName && validEmail && text.trim() !== "") {
      console.log("submitted");
      const dataToPost = {
        name: name,
        email: email,
        message: text,
      };
      const response = await fetch(
        "https://personal-website-64bb6-default-rtdb.firebaseio.com/messages.json",
        {
          method: "POST",
          body: JSON.stringify(dataToPost),
          headers: {
            "Content-Type": "application/json",
          },
        }
      );

      const data = response.json();
      console.log(data);
      setText("");
      resetName();
      resetEmail();
    }
  };

  const nameClass = !nameError ? "form-control" : "form-control invalid";

  const emailClass = !emailError ? "form-control" : "form-control invalid ";

  return (
    <Fragment>
      <Header title="Contact" />
      <Card>
        <form onSubmit={submitHandler}>
          <div className="control-group">
            <div className={nameClass}>
              <label htmlFor="name">First Name</label>
              <input
                type="text"
                id="name"
                value={name}
                onChange={nameHandler}
                onBlur={nameBlurHandler}
              />
              {nameError && <p className="error-text">Enter Valid Input</p>}
            </div>
          </div>
          <div className={emailClass}>
            <label htmlFor="name">E-Mail Address</label>
            <input
              type="text"
              id="name"
              value={email}
              onChange={emailHandler}
              onBlur={emailBlurHandler}
            />
            {emailError && <p className="error-text">Enter Valid Input</p>}
            <textarea
              placeholder="Write your message here..."
              onChange={onMessage}
              value={text}
            ></textarea>
          </div>
          <div className="form-actions">
            <button disabled={!showButton}>Submit</button>
          </div>
        </form>
      </Card>
    </Fragment>
  );
};

export default Contact;
